.main_event_list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Events_table {
  margin: 0;
  padding: 0;
}

.date_cell_day {
  background-color: #c5a7b9;
  color: white !important;
  font-size: 2rem !important;
  padding: 0 !important;
  margin: 0 !important;
  text-align: center !important;
  font-weight: bold;
}

.date_cell_day:hover {
  color: black !important;
}

.date_cell_houre {
  background: #c7c7c7;
  font-size: 18px !important;
  text-align: center !important;
  padding: 0 !important;
}

.table_row {
  height: 80px;
}

.cell {
  border-bottom: 0px solid rgb(221, 221, 221) !important;
  font-size: 1em !important;
  background: rgba(255, 255, 255);
}

.btn_book_event {
  width: 100px;
  background: linear-gradient(
    to bottom,
    rgb(187, 150, 187),
    rgb(187, 150, 187)
  );
  border: none;
  color: #fff;
  border-radius: 2em;
  padding: 0.3rem;
  cursor: pointer;
  float: left;
}

.table_row_main {
  background: rgb(146, 139, 150) !important;
}

.table_row_main_text {
  color: white !important;
  font-size: 1rem !important;
  font-weight: bold !important;
}

.cell_email {
  text-align: right !important;
}

.MuiTypography-root {
  text-align: right !important;
}

.description_continer {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  margin: 0.5rem;
}

.number_booked_p {
  text-align: end;
}

.booked_events_input_filter {
  width: 30%;
  margin: 0rem 0px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  text-align: center;
  color: rgb(23, 142, 210);
  vertical-align: top;
  font-size: 18px;
  border-radius: 6px;
  border: 1px solid #ccc;
  height: 30px;
  color: rgb(187, 150, 187);
}

.booked_events_input_filter:hover,
.booked_events_input_filter:focus {
  border-color: rgb(187, 150, 187);
  color: rgb(187, 150, 187);
}

.filer_continer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
